@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300display=swap);
.PersonContainer{max-width:335px;height:325px;border-radius:10px;border:1px solid gray;display:flex;justify-content:center;align-items:center;flex-direction:column;padding:15px 50px 30px 50px;width:100%}@media screen and (max-width: 915px){.PersonContainer{padding:15px 30px 30px 30px}}@media screen and (max-width: 500px){.PersonContainer{width:320px}}.PersonContainer .ContentHeader{display:inline-block;float:right}.PersonContainer .personDetail{display:flex;flex-direction:column;align-items:center;justify-content:center;grid-gap:10px}.PersonContainer .personDetail .name{font-size:28px}.PersonContainer .personDetail .total{font-size:24px}.PersonContainer .personDetail .active{font-size:20px;font-weight:bold}
.SearchContainer{position:relative;max-width:267px}.SearchContainer .search_icon{position:absolute;right:20px;top:20.5px}.SearchContainer .search{width:267px;height:56px;border:1px solid rgba(26,15,9,.1);box-sizing:border-box;border-radius:10px;padding:14.45px 20px;font-family:"Open Sans",sans-serif;font-style:normal;font-weight:normal;font-size:18px;line-height:150%;outline:none}
.CComHeader {
    width: 100vw;
    display: flex;
    justify-content: center;
    background-color: #272727;
    height: 100px;
    position: fixed;
    z-index: 10 !important;
    border-bottom: 1px solid grey;
    left: 0;
    top: 0;
}

.CmainHeader {
    width: 1311px;
    display: flex;
    top: 0;
    justify-content: space-between;
    height: 100px;
}

.CResItems {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    align-items: center;
}

.CHeaderlogo {
    display: flex;
    display: flex;
    align-items: center;
    font-size: 22px;
    color: #fff;
}

.CHeaderlogo>p {
    margin: 0;
    font-weight: bolder;
}

.Chomelogo {
    margin-right: 20px;
    cursor: pointer;
}

.CmainHeaderLeft {
    display: flex;
    align-items: center;
    color: white;
    font-size: 18px;
}

.CheaderItemRes {
    color: white;
    font-size: 25px;
    font-weight: bolder;
    text-align: center;
    margin: 20px;
}

.CheaderItem {
    margin-left: 40px;
    font-weight: 600;
    cursor: pointer;
}

.CHeaderRightItem {
    color: #fff;
    font-size: 30px;
    margin-left: 15px;
    cursor: pointer;
}

.CmainHeaderRight {
    display: flex;
    align-items: center;
}

.CResHeaderRightItem {
    color: #fff;
    font-size: 30px;
    margin-left: 15px;
    cursor: pointer;
}

.CResHeaderRightItemBar {
    color: #fff;
    font-size: 30px;
    margin-left: 15px;
    cursor: pointer;
    display: none;
}

.CHeaderRightItemCoin, .CResHeaderRightItemCoin {
    color: #fff;
    font-size: 35px;
    margin-left: 15px;
    cursor: pointer;
}

.CHeaderRightItemUser, .CResHeaderRightItemUser {
    color: #fff;
    font-size: 27px;
    margin-left: 15px;
    cursor: pointer;
}

.CResPanel {
    z-index: 10;
    position: absolute;
    top: 100px;
    background-color: #272727;
    width: 100%;
}

@media screen and (max-width:1315px) {
    .CmainHeader {
        padding: 0 30px 0 20px !important;
    }
}

@media screen and (min-width:800px) {
    .CResPanel {
        display: none !important;
    }
}

@media screen and (max-width:800px) {
    .CResHeaderRightItemBar {
        display: flex !important;
    }

    .CResHeaderRightItemUser, .CResHeaderRightItemCoin {
        margin: 0 !important;
    }

    .CResHeaderRightItem {
        margin: 0 30px !important;
    }

    .CHeaderRightItem, .CheaderItem, .CHeaderRightItemCoin, .CHeaderRightItemUser {
        display: none !important;
    }
}
.PartnerContainer{width:100%}.PartnerContainer .PartnerTitle{background:#000;border-radius:0px 0px 44px 44px;color:#fff;font-family:"Montserrat",sans-serif;font-style:normal;font-weight:bold;font-size:50px;line-height:61px;text-align:center;padding:58px 0 86px 0;margin-top:100px}.PartnerContainer .PartnerContent{margin:auto;max-width:1380px;margin-top:58px;display:flex;flex-direction:column}.PartnerContainer .PartnerContent .ContentHeader{display:inline-block;float:right}.PartnerContainer .PartnerContent .CardGroupContent{margin-top:68px;margin-bottom:5px;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:40px}@media screen and (max-width: 1385px){.PartnerContainer .PartnerContent .CardGroupContent{grid-template-columns:1fr 1fr 1fr}}@media screen and (max-width: 786px){.PartnerContainer .PartnerContent .CardGroupContent{grid-template-columns:1fr 1fr}}@media screen and (max-width: 500px){.PartnerContainer .PartnerContent .CardGroupContent{display:flex;justify-content:center;align-items:center;flex-direction:column}}
